<template>
    <div class="wrap">
        <div class="banner" :style="{height:clentHigh + 'px'}">
            <swiper :options="swiperOpt0" :style="{transform:'translate3d(0,'+ scrollHeight +'px,0)'}">
                <swiper-slide  v-for="(item,index) in slides" :key="index" :style="{backgroundImage:'url('+ item.imgSrc +')'}"></swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>

        <section class="white-block">
            <div class="container">
                <h3 class="center-title">云创生活</h3>
                <el-row :gutter="20">
                    <el-col :sm="8" v-for="(item,index) in goodsList" :key="index">
                        <g-item :thum-src = "item.imgSrc" href="/" :label="item.label" :title="item.title"></g-item>
                    </el-col>
                </el-row>
            </div>
        </section>
        <section class="section" :class="{'is-sticky':stickyNav}">
            <div class="container">
                <h3 class="center-title">贴心服务</h3>
                <el-row :gutter="20">
                    <el-col :md="6" :sm="8" v-for="(item,index) in Lifes" :key="index">
                        <g-item :thum-src = "item.imgSrc" href="/"  :title="item.title" ></g-item>
                    </el-col>
                </el-row>
            </div>
        </section>
        <section class="full-banner">
            <div class="container">
                <div class="mbox">
                    <div class="m-title"><span class="f-lg">云</span>享生活 品质未来</div>
                    <p>云创车易购，购车无忧  改变传统购车模式</p>
                    <el-button type="primary" size="large">美好生活，从现在开始</el-button>
                </div>
            </div>
        </section>

        <section class="section ">
            <div class="container">
                <h3 class="center-title"> 关于我们 </h3>
                <el-row :gutter="20">
                    <el-col :sm="8" v-for="(item,index) in aboutUs" :key="index">
                        <router-link class="card-item" to="/">
                            <div class="media" :style="{backgroundImage:'url('+ item.imgSrc +')'}"></div>
                            <div class="title">{{ item.title }}</div>
                            <div class="label">{{ item.label }}</div>
                            <div class="text-block " v-html="item.centent"> </div>
                        </router-link>
                    </el-col>
                </el-row>
                <!-- <swiper :options="swiperOpt3"  class="card-list">
                    <swiper-slide v-for="(item,index) in aboutUs" :key="index">
                        <router-link class="card-item" to="/">
                            <div class="media" :style="{backgroundImage:'url('+ item.imgSrc +')'}"></div>
                            <div class="title">{{ item.title }}</div>
                            <div class="label">{{ item.label }}</div>
                            <div class="text-block " v-html="item.centent"> </div>
                        </router-link>
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                </swiper> -->
            </div>
        </section>

        <v-footer></v-footer>
    </div>
</template>

<script>
import axios from 'axios'
import vFooter from '@/components/common/footer'
export default {
    components:{
        vFooter
    },
    data() {
        return {
            clentHigh : document.documentElement.clientHeight ,
            scrollHeight: 0,
            stickyNav:false,
            goodsList:[],
            Lifes:[],
            slides: [],
            aboutUs: [],
            swiperOpt0: {
                pagination: '.swiper-pagination',
                paginationClickable: true,
                autoplay: 3500,
                effect: 'fade',
                loop:true
            },
            swiperOpt3: {
                slidesPerView: 3,
                spaceBetween: 20,
                pagination: '.swiper-pagination',
                paginationClickable: true,
            },
            
        }
    },
    mounted() {
        this.getdata();
        window.addEventListener('scroll', this.scrollPage)
    },
    methods: {
        getdata() {
            let _this = this;
            axios.get('static/data.json').then(function(res) {
                
                _this.goodsList = res.data.goodsList;
                _this.Lifes = res.data.Lifes;
                _this.slides = res.data.slides;
                _this.aboutUs = res.data.aboutUs;

            }).catch(function(res) {
                console.log(res);
            });
        },
        scrollPage() {
            this.scroll = document.body.scrollTop;
            // console.log(this.scroll)

            if(this.scroll>this.clentHigh){
                this.stickyNav = true;

            }else {
                this.stickyNav = false
                this.scrollHeight = this.scroll / 5
            }
        },
    }
}
</script>

<style lang="scss">
@import 'src/sass/into';

.banner {
    width: 100%;
    .swiper-container{
        height: 100%;
        z-index: 0;
        position: relative;
        .swiper-slide{
            height: 100%;
            background-color: $bg;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;
        }
    }
}

.card-list{
    padding-bottom: 30px;
}

.full-banner{
    background: url('/static/thum/image_1.jpg') no-repeat center center;
    background-size: cover;
    height: 600px;
    .container{
        height: 100%;
        position: relative;
    }
    .mbox{
        padding: 30px;
        @include right-middle($spacing);
        background: rgba(#ffffff, .9);
        box-shadow: 1px 2px 8px rgba(#000,.2);
        border-radius: 5px;
        .m-title{
            font-size: 32px;
            margin-bottom: 10px;
            color: $text-dark;
            .f-lg{
                font-size: 48px;
                color: $blue;
            }
        }
        p{
            font-size: 20px;
            color: $text-light;
            line-height: 40px;
            margin-bottom: 20px;
        }
        .el-button{
            border-radius: 50px;
        }
    }
}

</style>



